import React from 'react';
import { List, Divider, Typography } from '@mui/material';
import TaskItem from './TaskItem';

const TaskList = ({ tasks, onEdit, refresh }) => {
  if (tasks.length === 0) {
    return <Typography variant="h6" align="center" color="text.secondary">No tasks yet.</Typography>;
  }
  return (
    <List>
      {tasks.map((task, idx) => (
        <React.Fragment key={task._id}>
          <TaskItem task={task} onEdit={onEdit} refresh={refresh} />
          {idx < tasks.length - 1 && <Divider />}
        </React.Fragment>
      ))}
    </List>
  );
};

export default TaskList;
