import React from 'react';
import { ListItem, ListItemText, IconButton, Chip, Tooltip, Stack } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import PriorityHighIcon from '@mui/icons-material/PriorityHigh';
import ScheduleIcon from '@mui/icons-material/Schedule';
import axios from 'axios';
import { toast } from 'react-toastify';
import dayjs from 'dayjs';

const statusColors = {
  pending: 'warning',
  'in-progress': 'info',
  completed: 'success'
};

const priorityColors = {
  low: 'default',
  medium: 'primary',
  high: 'error'
};

const TaskItem = ({ task, onEdit, refresh }) => {
  const handleDelete = async () => {
    if (!window.confirm('Are you sure you want to delete this task?')) return;
    try {
      await axios.delete(`/api/tasks/${task._id}`);
      toast.success('Task deleted!');
      refresh();
    } catch {
      toast.error('Failed to delete.');
    }
  };

  return (
    <ListItem
      secondaryAction={
        <Stack direction="row" spacing={0.5}>
          <Tooltip title="Edit">
            <IconButton edge="end" onClick={() => onEdit(task)}>
              <EditIcon color="primary" />
            </IconButton>
          </Tooltip>
          <Tooltip title="Delete">
            <IconButton edge="end" onClick={handleDelete}>
              <DeleteIcon color="error" />
            </IconButton>
          </Tooltip>
        </Stack>
      }
      sx={{ bgcolor: 'background.paper', borderRadius: 2, mb: 1, boxShadow: 1 }}
    >
      <ListItemText
        primary={
          <span style={task.status === 'completed' ? { textDecoration: 'line-through' } : {}}>
            {task.title}
          </span>
        }
        secondary={
          <>
            <Stack direction="row" spacing={1} alignItems="center" mt={1}>
              <Chip
                variant="outlined"
                size="small"
                icon={<PriorityHighIcon />}
                label={`Priority: ${task.priority}`}
                color={priorityColors[task.priority]}
              />
              <Chip
                variant="outlined"
                icon={<ScheduleIcon />}
                size="small"
                label={`Deadline: ${dayjs(task.deadline).format('DD MMM, HH:mm')}`}
                color="secondary"
              />
              <Chip size="small" label={task.status} color={statusColors[task.status]} />
              {task.recurring?.pattern !== 'none' && (
                <Chip size="small" label={`Recurring: ${task.recurring.pattern}`} color="info" />
              )}
            </Stack>
            {task.subtasks && task.subtasks.length > 0 && (
              <Stack mt={1} spacing={0.5}>
                <em>Subtasks:</em>
                {task.subtasks.map((subtask, i) => (
                  <span
                    key={i}
                    style={
                      subtask.status === 'completed'
                        ? { textDecoration: 'line-through', color: 'gray' }
                        : {}
                    }
                  >
                    &bull; {subtask.title} ({subtask.status})
                  </span>
                ))}
              </Stack>
            )}
          </>
        }
      />
    </ListItem>
  );
};

export default TaskItem;
