import React, { useState, useEffect } from 'react';
import { ThemeProvider } from '@mui/material/styles';
import { Container, Typography, Fab } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import TaskList from './components/TaskList';
import AddEditTaskModal from './components/AddEditTaskModal';
import axios from 'axios';
import theme from './theme';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function App() {
  const [tasks, setTasks] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [editingTask, setEditingTask] = useState(null);

  const fetchTasks = async () => {
    try {
      const res = await axios.get('/api/tasks');
      setTasks(res.data);
    } catch {
      toast.error('Failed to load tasks');
    }
  };

  useEffect(() => {
    fetchTasks();
  }, []);

  const handleAdd = () => {
    setEditingTask(null);
    setModalOpen(true);
  };

  const handleEdit = (task) => {
    setEditingTask(task);
    setModalOpen(true);
  };

  const handleSaved = () => {
    fetchTasks();
    setModalOpen(false);
  };

  return (
    <ThemeProvider theme={theme}>
      <Container maxWidth="md" sx={{ borderRadius: 2, boxShadow: 1, p: 4, mt: 5, background: 'rgba(30,34,45,0.92)' }}>
        <Typography variant="h3" align="center" gutterBottom fontWeight={700} color="primary">
          Advanced ToDo List
        </Typography>
        <TaskList tasks={tasks} onEdit={handleEdit} refresh={fetchTasks} />
        <Fab
          color="secondary"
          aria-label="add"
          onClick={handleAdd}
          sx={{ position: 'fixed', bottom: 40, right: 40 }}
        >
          <AddIcon />
        </Fab>
        <AddEditTaskModal open={modalOpen} onClose={() => setModalOpen(false)} onSaved={handleSaved} editingTask={editingTask} />
        <ToastContainer />
      </Container>
    </ThemeProvider>
  );
}

export default App;
